@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
}

.makrinePic {
  height: 40vmin;
  
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* color: #0b7a7a */
  
}
#homeText {
  margin-top: 5%;
  font-family: 'Courier New', Courier, monospace;
}

.social-icons {
  padding: 0 1vh;
}

.social-icons:hover {
  padding: 0 3vh;
  opacity: 0.5;

}

a {
  text-decoration: none;
  color: black;
}


@media screen and (min-width: 601px) {
  #homeText {
    font-size: 2vw;
  }
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 600px) {
  #homeText {
    font-size: 5vw;
  }
}

.content-container {
  position: relative;
  width: 100%;
  display: grid;
  grid-gap: 45px;
}

.content-container .box {
  position: relative;
  color: #fff;
  height: auto; /*400px*/
  display: flex;
  text-align: left;
  background-color: #04101a;
}

.content {
  padding: 0 40px;
  position: relative;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.content h2 {
  font-weight: 700;
  font-size: 30px;
  margin-bottom: 30px;
}
.content p {
  letter-spacing: 1px;
  font-size: 18px;
} 
 .wrapper{
  margin-top: 5%;
}

.menu{
  display: inline-block;
  padding: 15px;
  width: 200px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  font-family: "'Courier New', Courier, monospace";
  text-decoration: none;
  color: #fff;
  border: 1px solid #fff;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;
  cursor: pointer;
  margin-left: 10px;
  margin-top: 10px;
}

#workXPMenu {
  font-size: 15px;
}

.menu span{
  position: relative;
  z-index: 3;
}

.menu:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background: #000;
  transition: all .35s;
}

.menu:hover{
  color: #fff;
}

.menu:hover:after{
  height: 100%;
}

.scroll-to-top {
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
  cursor: pointer;
}

footer {
  color: #fff;
  background-color: #04101a;
  padding: 10px 0;
}

.rights {
  padding: 0  40px;
}


