.container {
  height: 500px;
  margin: 0 auto;
  position: relative;
  padding-bottom: 15%;
}



.rb-container {
  font-family: "PT Sans", sans-serif;
  width: 100%;
  margin: auto;
  display: block;
  position: relative;
}

.rb-container ul.rb {
  margin: 2.5em 0;
  padding: 0;
  display: inline-block;
}

.rb-container ul.rb li {
  list-style: none;
  margin: auto;
  /* margin-left: 10em; */
  min-height: 50px;
  border-left: 1px dashed #fff;
  padding: 0 0 50px 30px;
  position: relative;
}

.rb-container ul.rb li:last-child {
  border-left: 0;
}

.rb-container ul.rb li::before {
  position: absolute;
  left: -18px;
  top: -5px;
  content: " ";
  border: 8px solid rgba(255, 255, 255, 1);
  border-radius: 500%;
  background: #50d890;
  height: 20px;
  width: 20px;
  transition: all 500ms ease-in-out;
}

.rb-container ul.rb li:hover::before {
  border-color: #232931;
  transition: all 1000ms ease-in-out;
}

ul.rb li .timestamp {
  color: #50d890;
  position: relative;
  width: 250px;
  font-size: 12px;
}

.item-title {
  color: #fff;
  margin-top: 3%;
}


.work-desc t{
  font-size: small;
  margin-top: 2%;
  margin-left: 3%;
  display: list-item;          
  list-style-position: inside;
  list-style: square;
}

a {
  color: orange;
  font-weight: bold;
}

a:visited {
  color: orange;
}

